<template>
    <div class="home">
        <h1>Welcome to home</h1>
        <h1>{{ inputA }}</h1>
        <input type="text" v-model="inputA">
        <input type="button" @click="get" value="get">
        <select name="" id="" class="form_control" v-model="region" @change="changeRegion"> <!--v-model은 j라서 옵션이 jeju로 되어있음-->
            <option v-for="(d,index) in list" :key="index" :value="d.v">{{d.t}}</option>
        </select>

        <input type="button" value="tableShow?" @click="show">
        <table class="table test_table" v-show="tableShow">
            <tr v-for="(d,index) in list" :key="index">
                <td>{{d.v}}</td>
                <td>{{d.t}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            inputA : "ABDC",
            list : [
                {v : "S", t : 'Seoul'},
                {v : "J", t : 'Jeju'},
                {v : "B", t : 'Busan'},
            ],
            region : "J",
            tableShow : false,
        }
    },

    methods: {
        get() {
            this.inputA = this.inputA + " : 실행완료";
        },
    
        changeRegion() {
            console.log(this.region)
        },

        show() {
            if(this.tableShow == false) {
                this.tableShow = true;
            }else{
                this.tableShow = false;
            }
        }
    },

    watch: { //어떤 data가 변경이 이루어 질 때 이 안에있는 함수들이 실행됨 
        inputA() { //inputA가 변경될 때 실행
            console.log("A 변경완료")
        }
    }
}
</script>